import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MoveInOutService } from '../../../../api/services/move-in-out.service';
import { UserService } from '../../../../api/services/user.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';
import * as _ from 'underscore';

@Component({
  selector: 'app-user-moveout',
  templateUrl: './user-moveout.component.html',
  styleUrls: ['./user-moveout.component.scss']
})
export class UserMoveoutComponent implements OnInit {

  moveout:any;

  statusTypeData:any;
  blocksData:any;
  unitData:any;
  raisedByData:any;

  apartmentBlockUnitId:any;

  apartmentBlockNumber:string = "";
  apartmentBlockUnitNumber:string = "";

  isMoveoutSubmitted:boolean = true;

  isEdit:boolean = false;

  isError:boolean = false;
  alertMessage:string = "";

  isDataLoaded:boolean = false;

  userName:string = "";

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private moveInOutService:MoveInOutService,
  	private userService:UserService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  getBlock() {
    this.apartmentService.getApartmentBlockUnitById(parseInt(this.apartmentBlockUnitId)).subscribe((res:any) => {
        this.apartmentBlockNumber = res[0].apartmentBlockNumber
        this.apartmentBlockUnitNumber = res[0].apartmentBlockUnitNumber
        this.isDataLoaded = true;
    },
    error => {
      console.log(error);
    }
    );
  }



  submitAddMoveOutForm(form) {

    this.isMoveoutSubmitted = false;

    if(!this.isEdit){


      let details = {

          "outDate": this.moveout.inDate,
          "outTime": moment(this.moveout.inTime).format('HH:mm:ss'),
          "familyCount": this.moveout.familyCount,
          "comments": this.moveout.comment,
          "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
          "reqUserId": parseInt(this.moveout.reqUserId),
          "statusId": 306, //default approve status
          "approvedBy": null,
          "approvedDate": null,
          "noDue": true,
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": new Date().toISOString(),
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": new Date().toISOString(),
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),

      }

      this.moveInOutService.addMoveOut(details).subscribe((res:any) => {

        if(res.message){
          this.isMoveoutSubmitted = true;
          this.sharedService.setAlertMessage("Moveout added successfully");
         }
         else {
          this.isMoveoutSubmitted = true;
          this.isError = true;
          this.alertMessage = res.errorMessage;
         }

      },
      error => {
        this.isMoveoutSubmitted = true;
      });


    }

    else {

      let details = {

          "id": this.moveout.id,
          "outDate": this.moveout.inDate,
          "outTime": moment(this.moveout.inTime).format('HH:mm:ss'),
          "familyCount": this.moveout.familyCount,
          "comments": this.moveout.comment,
          "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
          "reqUserId": parseInt(this.moveout.reqUserId),
          "statusId": 306, //default approve status
          "approvedBy": null,
          "approvedDate": null,
          "noDue": true,
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": new Date().toISOString(),
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": new Date().toISOString(),
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),

      }

      this.moveInOutService.updateMoveOut(details).subscribe((res:any) => {

        if(res.message){
          this.isMoveoutSubmitted = true;
          this.sharedService.setAlertMessage("Moveout updated successfully");
          this.router.navigate(['ams/moveinout-tracker/moveout']);
         }
         else {
          this.isMoveoutSubmitted = true;
          this.isError = true;
          this.alertMessage = res.errorMessage;
         }

      },
      error => {
        this.isMoveoutSubmitted = true;
      });

    }

  }

  ngOnInit() {

  	this.moveout = {};

  	this.moveout.statusId = "";
  	this.moveout.reqUserId = "";
    this.moveout.comment = "";

    if(this.route.params['value'].id != undefined){
      
      this.isEdit = true;

      this.moveInOutService.getMoveOutById(this.route.params['value'].id).subscribe((res:any) => {
          
        this.moveout = res[0];
        this.apartmentBlockUnitId = "" + this.moveout.apartmentBlockUnitId;
        this.getBlock();

        this.moveout.outTime = moment(this.moveout.outTime, 'HH:mm:ss').toDate();

      },
      error => {
        
      });

    }
    else {

      this.apartmentService.getAllApartmentBlockUnitUsersByUserId(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {

        if(res == undefined || res.length == 0){
          this.apartmentBlockUnitId = ""
        }
        else {
          this.apartmentBlockUnitId = "" + res[0].apartmentBlockUnitId
          this.getBlock();
        }

      },
      error => {
        
      });

    }

  	//get apartment blocks
    this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
      this.blocksData = res;
    });

    //status 
    this.lookupService.getLookupValueByLookupTypeId(9).subscribe((res:any) => {
          
      this.statusTypeData = res.filter( item => {
          return item.isActive;
      });

    },
    error => {
      
    });

    //get all users
    this.userService.getAllUsersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
       
      this.raisedByData = res;

      if(this.cookieService.get('userRole') == 'Tenant' || this.cookieService.get('userRole') == 'Owner'){

       this.raisedByData = res.filter(item => {
          return item.userId == parseInt(this.cookieService.get('userId'));
       });

       this.moveout.reqUserId = this.raisedByData[0].userId;

       this.userName = this.raisedByData[0].firstName+' '+ this.raisedByData[0].lastName;

      }
      else {

        this.raisedByData = res.filter(item => {
          return item.userId == parseInt(this.moveout.reqUserId)
        });

        this.userName = this.raisedByData[0].firstName+' '+ this.raisedByData[0].lastName;

      }

    },
    error => {
      
    }
    );

  	this.apartmentBlockUnitId = "";

  }

}
