import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';


import { MoveInOutService } from '../../../../api/services/move-in-out.service';
import { UserService } from '../../../../api/services/user.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';
import * as _ from 'underscore';

@Component({
  selector: 'app-user-movein',
  templateUrl: './user-movein.component.html',
  styleUrls: ['./user-movein.component.scss']
})

export class UserMoveinComponent implements OnInit {

  movein:any;

  statusTypeData:any;
  blocksData:any;
  unitData:any;
  raisedByData:any;

  apartmentBlockUnitId:any;

  apartmentBlockNumber:string = "";
  apartmentBlockUnitNumber:string = "";

  isMoveinSubmitted:boolean = true;

  isEdit:boolean = false;

  isError:boolean = false;
  alertMessage:string = "";

  isDataLoaded:boolean = false;

  userName:string = "";

  documentConfigId:string = "";
  documentDataList:any;

  documentComment:string = "";

  isFileAdded:boolean = false;
  selectedFile: File;
  fileList: File[] = [];
  listOfFiles: any[] = [];

  uploadedDocumentList:any;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private moveInOutService:MoveInOutService,
  	private userService:UserService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }


  getBlock() {
    this.apartmentService.getApartmentBlockUnitById(parseInt(this.apartmentBlockUnitId)).subscribe((res:any) => {
        this.apartmentBlockNumber = res[0].apartmentBlockNumber
        this.apartmentBlockUnitNumber = res[0].apartmentBlockUnitNumber
        this.isDataLoaded = true;
    },
    error => {
      console.log(error);
    }
    );
  }

  uploadFile(event){
    this.isFileAdded = true;

    for (var i = 0; i <= event.target.files.length - 1; i++) {
      var selectedFile = event.target.files[i];
      this.fileList.push(selectedFile);
      this.listOfFiles.push({'name':selectedFile.name, 'size':selectedFile.size});

      console.log(this.fileList[0]);
    }

  }

  deleteFile(index){
    // Delete the item from fileNames list
    this.listOfFiles.splice(index, 1);
    // delete file from FileList
    this.fileList.splice(index, 1);

    this.selectedFile = null;

    if(this.fileList.length == 0 && this.listOfFiles.length == 0){
      this.isFileAdded = false;
    }

  }

  bytesToSize(bytes) {
   if(bytes < 1024) return bytes + " Bytes";
    else if(bytes < 1048576) return(bytes / 1024).toFixed(3) + " KB";
    else if(bytes < 1073741824) return(bytes / 1048576).toFixed(3) + " MB";
    else return(bytes / 1073741824).toFixed(3) + " GB";
  }

  isFileEmpty(){
    return this.fileList.length == 0
  }

  submitAddMoveInForm(form) {

    this.isMoveinSubmitted = false;

    if(!this.isEdit){

      let details = {

        "inDate": this.movein.inDate,
        "inTime": moment(this.movein.inTime).format('HH:mm:ss'),
        "familyCount": this.movein.familyCount,
        "comments": this.movein.comment,
        "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
        "reqUserId": parseInt(this.movein.reqUserId),
        "statusId": 306, //default approve status
        "approvedBy": null,
        "approvedDate": null,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString(),
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),

      }

      this.moveInOutService.addMoveIn(details).subscribe((res:any) => {

        if(res.message){

          let fileDetails = {

            "moveInId": parseInt(res.message),
            "moveInOutConfigId": parseInt(this.documentConfigId),
            "fileDetailsId": 0,
            "comments": this.documentComment,
            "isActive": true,
            "insertedBy": parseInt(this.cookieService.get('userId')),
            "insertedOn": new Date().toISOString(),
            "updatedBy": null,
            "updatedOn": null

          }

          let fileParams = {
            moveIn : fileDetails,
            file: this.fileList[0],
            apartmentId: parseInt(this.cookieService.get('apartmentId'))
          }

          this.moveInOutService.addMoveInDetails(fileParams).subscribe((res:any) => {

            if(res.message){
              this.isMoveinSubmitted = true;
              this.sharedService.setAlertMessage("Movein added successfully");
            }
            else {
              this.isMoveinSubmitted = true;
              this.isError = true;
              this.alertMessage = res.errorMessage;
            }

          },
          error => {

            this.isMoveinSubmitted = true;

          });

          

       }
       else {

        this.isMoveinSubmitted = true;
        this.isError = true;
        this.alertMessage = res.errorMessage;
           
       }

      },
      error => {

        this.isMoveinSubmitted = true;

      });

    }

    else {

      let details = {

        "id": this.movein.id,
        "inDate": this.movein.inDate,
        "inTime": moment(this.movein.inTime).format('HH:mm:ss'),
        "familyCount": this.movein.familyCount,
        "comments": this.movein.comment,
        "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
        "reqUserId": parseInt(this.movein.reqUserId),
        "statusId": 306, //default approve status
        "approvedBy": null,
        "approvedDate": null,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString(),
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),

      }

      this.moveInOutService.updateMoveIn(details).subscribe((res:any) => {

        if(res.message){

          this.isMoveinSubmitted = true;
          this.sharedService.setAlertMessage("Movein updated successfully");
          this.router.navigate(['ams/moveinout-tracker/movein']);

       }
       else {

        this.isMoveinSubmitted = true;
        this.isError = true;
        this.alertMessage = res.errorMessage;
           
       }

      },
      error => {

        this.isMoveinSubmitted = true;

      });


    }
    

  }

  ngOnInit() {

  	this.movein = {};

  	this.movein.statusId = "";
  	this.movein.reqUserId = "";
    this.movein.comment = "";
    this.apartmentBlockUnitId = "";

    if(this.route.params['value'].id != undefined) {
      
      this.isEdit = true;

      this.moveInOutService.getMoveInById(this.route.params['value'].id).subscribe((res:any) => {
          
        this.movein = res[0];
        this.apartmentBlockUnitId = "" + this.movein.apartmentBlockUnitId;
        this.getBlock();

        this.movein.inTime = moment(this.movein.inTime, 'HH:mm:ss').toDate();

      },
      error => {
        
      }
      );

    }
    else {

      this.apartmentService.getAllApartmentBlockUnitUsersByUserId(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {

        if(res == undefined || res.length == 0){
          this.apartmentBlockUnitId = ""
        }
        else {
          this.apartmentBlockUnitId = "" + res[0].apartmentBlockUnitId
          this.getBlock();
        }

      },
      error => {
        
      });

    }

  	//get apartment blocks
    this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
      this.blocksData = res;
    });

    //status 
    this.lookupService.getLookupValueByLookupTypeId(73).subscribe((res:any) => {
          
      this.statusTypeData = res.filter( item => {
          return item.isActive;
      });

    },
    error => {
      
    });

    this.moveInOutService.getMoveInOutConfigByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.documentDataList = res.filter(item => {
        return item.isActive;
      });


    }, error => {
        console.log(error);
    });



    //get all users
    this.userService.getAllUsersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
       
      this.raisedByData = res;

      if(this.cookieService.get('userRole') == 'Tenant' || this.cookieService.get('userRole') == 'Owner'){

       this.raisedByData = res.filter(item => {
          return item.userId == parseInt(this.cookieService.get('userId'));
       });

       this.movein.reqUserId = this.raisedByData[0].userId;

       this.userName = this.raisedByData[0].firstName+' '+ this.raisedByData[0].lastName;

      }
      else {

        this.raisedByData = res.filter(item => {
          return item.userId == parseInt(this.movein.reqUserId)
        });

        this.userName = this.raisedByData[0].firstName+' '+ this.raisedByData[0].lastName;

      }

    },
    error => {
      
    }
    );


  }

}
